html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-primary);
}

:root {
  --blue-900: #002547;
  --blue-800: #073464;
  --blue-700: #124982;
  --blue-600: #2d6095;
  --blue-500: #487cb1;
  --blue-400: #6698cc;
  --blue-300: #9dbee1;
  --blue-200: #c6d6e6;
  --blue-100: #dce3e9;
  --yellow-900: #663d00;
  --yellow-800: #995c00;
  --yellow-700: #c97a03;
  --yellow-600: #ee9611;
  --yellow-500: #e8a94a;
  --yellow-400: #eebf77;
  --yellow-300: #ffd494;
  --yellow-200: #ffe8c7;
  --yellow-100: #fff2e0;
}

:root {
  --font-primary: "Proxima Nova", "Trebuchet MS", sans-serif;
}

.grid {
  display: grid;
}

.grid-col__2fr-1fr {
  grid-template-columns: 2fr 1fr;
}
.grid-col__3fr-1fr {
  grid-template-columns: 3fr 1fr;
}

.flex {
  display: flex;
}
.flex-col {
  display: flex;
  flex-direction: column;
}

.flow-wrap {
  flex-flow: wrap;
}

.jc-sb {
  justify-content: space-between;
}
.jc-sa {
  justify-content: space-around;
}
.jc-c {
  justify-content: center;
}
.jc-start {
  justify-content: flex-start;
}
.jc-end {
  justify-content: flex-end;
}

.ai-start {
  align-items: flex-start;
}
.ai-end {
  align-items: flex-end;
}
.ai-c {
  align-items: center;
}
.ai-base {
  align-items: baseline;
}

.gap-01 {
  gap: 0.1rem;
}
.gap-02 {
  gap: 0.2rem;
}
.gap-03 {
  gap: 0.3rem;
}
.gap-05 {
  gap: 0.5rem;
}
.gap-1 {
  gap: 1rem;
}
.gap-15 {
  gap: 1.5rem;
}
.gap-2 {
  gap: 2rem;
}
.gap-3 {
  gap: 3rem;
}
.gap-4 {
  gap: 4rem;
}
.gap-5 {
  gap: 5rem;
}

.w-100 {
  width: 100%;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4375rem;
  padding: 0.4375rem 0.875rem;
  border-radius: 0.3125rem;
  font-weight: 600;
}
@media (max-width: 377px) {
  .button {
    font-size: 0.9rem;
  }
}
.button--primary {
  background-color: var(--yellow-400);
  color: var(--blue-900);
}

.card {
  background-color: lightcoral;
  padding: 1.25rem;
}

.navbar {
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 99999;
  background-color: var(--blue-900);
  padding: 0.9375rem;
}
.navbar__logo-container {
  display: inline-flex;
}
@media (max-width: 585px) {
  .navbar__logo {
    max-width: 150px;
  }
}
@media (max-width: 349px) {
  .navbar__logo {
    display: none;
  }
}
.navbar__logo--mobile {
  display: none;
}
@media (max-width: 349px) {
  .navbar__logo--mobile {
    display: block;
  }
}